@import url(https://fonts.googleapis.com/css2?family=Comfortaa&family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
header {
  height: 15vh;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 3px solid black;
}

header #title {
  margin: auto 1rem;
}

header h1 {
  font-family: 'Comfortaa', cursive;
  font-size: 3rem;
  letter-spacing: 0.042rem;
  font-weight: 500;
}

header h3 {
  font-family: 'Lato', sans-serif;
  font-size: 1.6rem;
  font-weight: 500;
}

#score {
  display: flex;
  font-family: 'Lato', sans-serif;
  width: 200px;
  margin: auto 1rem;
}

.scoreCard {
  flex-basis: 0;
  flex-grow: 1;
  margin: auto 0.5rem;
  text-align: center;
  border: 2px solid black;
  padding: 0.5rem 0;
  border-radius: 10px;
}

.scoreCard p.value {
  font-size: 3rem;
  line-height: 1;
}

.card {
  box-sizing: border-box;
  width: 25vh;
  height: 38vh;
  position: relative;
}

.card img {
  width: 100%;
  height: 100%;
  border: 3px solid black;
  border-radius: 20px;
  object-fit: cover;
  transition: transform 0.2s linear;
  cursor: pointer;
}

.card img:hover {
  transform: scale(1.03);
}

main{
    height: 85vh;
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-template-rows: repeat(2, auto);
    align-items: center;
    justify-content: space-evenly;
}
#gameOver {
  grid-column-start: 1;
  grid-column-end: 6;
  height: 85vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#gameOver h2 {
  font-size: 8rem;
  font-family: 'Montserrat', sans-serif;
  color: red;
}

#gameOver p {
  font-family: 'Montserrat', sans-serif;
  font-size: 5rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

